.pp{
    display: flex;
    width: 100%;
}
.img{
    width: 50%;
    transform:scaleX(-1) ;
    padding-right: 2vw;
}
.tt{
    width: 50%;
}
.t3{
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    padding-top: 8vw;
    padding-left: 4vw;
    font-size: 4vw;
}
.t1{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    padding-left: 4vw;
    padding-top: 16vw;
}
.l1{
    text-decoration: none;
    padding-left: 15vw;
}
.change i::after{
    content: "TextWiz... ";
    padding-left: 15vw;
    font-size: larger;
    animation: chan 2s ease-in-out infinite;
}
@keyframes chan{
    0%{
        content: "Textwiz...";
    }
    20%{
        content: "tExtwiz...";
    }
    40%{
        content: "teXtwiz...";
    }
    55%{
        content: "texTwiz...";
    }
    70%{
        content: "textWiz...";
    }
    80%{
        content: "textwIz...";
    }
    100%{
        content: "textwiZ...";
    }
}
